const Wallet = function () {
  const b = 'wallet';
  let s;
  return {
    settings: {
      $b: $(bemS(b))
    },
    init: function () {
      s = this.settings;
      this.bindUIActions();
    },
    bindUIActions: function () {
      $(document).on('click', bemS(b, 'head'), function (e) {
        $(this).closest(bemS(b)).toggleClass(bemC(b, '', 'open'));
      });
    }
  }
}();


$(function () {
  FormFieldLbl.init();
  LineChart.init();
  FormSelect.init();
  SelectSm.init();
  Wallet.init();
  FormFieldSm.init();
  FieldFile.init();

  $('[data-toggle="popover"]').popover();
});